import {mapActions, mapState} from 'vuex';

import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import chatTyping from '@/components/chats/assets/typing.vue'
import typing from "@/components/chats/assets/typing";
import contacts from '@/components/contacts/index.vue'

export default {
  name: 'chatTopheader',
  props: {
    chat: Object,
    u: String,
    roomInfo: false,
    aboutUser: false
  },

  components: {
    chatName,
    chatIcon,
    chatTyping,
    contacts
  },

  data: function () {

    return {

      menuItems: [
        {
          click: "callupVideoHandler",
          title: "Video Call",
          icon: "fas fa-camera"
        },

        {
          click: "callupHandler",
          title: "Call",
          icon: "fas fa-phone"
        },
      ],

      menuItemsRoom: [
        {
          click: "AddMember",
          title: "Add",
          icon: "fas fa-user-plus"
        },

        {
          click: "MuteRoom",
          title: "Mute",
          icon: "fas fa-bell-slash"
        },
        {
          click: "LeaveFromRoom",
          title: "Leave",
          icon: "fas fa-sign-out-alt"
        },
      ],


      loading: false,
      typing: false,
      userinfo: null,
      aboutUserShow: false
    }

  },


  watch: {
    //$route: 'getdata'
  },

  mounted: function () {
    this.getuserinfo()
  },


  computed: mapState({
    auth: state => state.auth,

    m_chat: function () {

      if (this.chat && this.chat.roomId) {
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)
        return m_chat || {}
      }
    },

    m_chat_typing: function () {

      if (!this.chat) return false

      var chat = this.chat.roomId
      var typing = this.$store.state.typing[chat] || {}

      var userTyping = _.find(typing, (t, i) => {

        if (t) {

          if (i == this.core.user.userinfo?.id) return false

          return true

        }

      })

      return userTyping || false
    },


  }),

  methods: {
    getuserinfo: function () {
      if (this.u) {
        this.core.user.usersInfo(this.u).then(info => {
          this.userinfo = info[0]
        })
      }
    },
    addMember() {
      this.modalInviteUser()
    },
    menuItemClickHandler(item, rowObject) {
      if (item.click === 'AddMember') {
        this.$emit('addMember', true)
        this.$refs.dropdownMenu.hidePopup();
      }
      if (item.click === 'MuteRoom') {

      }
      if (item.click === 'LeaveFromRoom') {
        this.core.mtrx.client.kick(this.m_chat.roomId, this.m_chat.myUserId, 'leave').then(result => {
          this.core.mtrx.client.forget(this.m_chat.roomId, true)
        }).then(r => {
          this.$emit('closeAboutRoom', false)
        })
        this.$router.push({path: '/chats'})

        this.$refs.dropdownMenu.hidePopup();
      }
    },
    modalInviteUser() {
      this.inviteUserOpened = true
    },

    closeModal() {
      this.inviteUserOpened = false
    },

    closeContactModal(value) {
      this.inviteUserOpened = value
    },
    callupHandler(rowObject) {

    },

    callupVideoHandler(rowObject) {

    },
  }

}